import React, { useEffect, useState } from 'react'
import './style.css'
import * as backimgtop from '../../assets/Clean Walk/Levels_Screen/BG_top.svg';
import QrReader from 'react-qr-reader'

import * as clue1 from '../../assets/Clean Walk/Clues/Clue_lev_01.png'
import * as clue2 from '../../assets/Clean Walk/Clues/Clue_lev_02.png'
import * as clue3 from '../../assets/Clean Walk/Clues/Clue_lev_03.png'
import * as clue4 from '../../assets/Clean Walk/Clues/Clue_lev_06.png'
import * as clue5 from '../../assets/Clean Walk/Clues/Clue_lev_05.png'
import * as clue6 from '../../assets/Clean Walk/Clues/Clue_lev_04.png'
import * as clue7 from '../../assets/Clean Walk/Clues/Clue_lev_07.png'
import * as clue8 from '../../assets/Clean Walk/Clues/Clue_lev_08.png'
import * as clue9 from '../../assets/Clean Walk/Clues/Clue_lev_09.png'

import { useParams } from 'react-router-dom';
import { useHistory } from 'react-router-dom';

import * as menu from '../../assets/Clean Walk/Video/VID_next.png'
import * as close from '../../assets/Clean Walk/Video/VID_next.png'
import Spinner from 'components/Common/Spinner'
import { loader } from 'components/Common/core'
import HeaderComponent from 'components/Common/HeaderComponent'

const listItems = [{
  "Level_name": "Level 1",
  "Task_Name": "WALK ON",
  "image": clue1,
  "Location": "Nike Store",
},
{
  "Level_name": "Level 2",
  "Task_Name": "TAKE A RIDE",
  "image": clue2,
  "Location": "Church Street entrance of MG Road Metro Station",
},
{
  "Level_name": "Level 3",
  "Task_Name": "PEDAL AHEAD",
  "image": clue3,
  "Location": "Cycle stand",
},
{
  "Level_name": "Level 4",
  "Task_Name": "BE A HERO",
  "image": clue4,
  "Location": "Rooster Guitars",
},
{
  "Level_name": "Level 5",
  "Task_Name": "TURN A PAGE",
  "image": clue5,
  "Location": "Bookstores (Blossoms, Goobes, Bookworm)",
},
{
  "Level_name": "Level 6",
  "Task_Name": "STRIKE A CHORD",
  "image": clue6,
  "Location": "Strike a Chord ",
},
{
  "Level_name": "Level 7",
  "Task_Name": "TAKE A WHIFF",
  "image": clue7,
  "Location": "Coffee shops",
},
{
  "Level_name": "Level 8",
  "Task_Name": "TAKE A NOTE",
  "image": clue8,
  "Location": "KSPCB Office",
},
{
  "Level_name": "Level 9",
  "Task_Name": "A NEW BEGINNING",
  "image": clue9,
  "Location": "KSPCB Office",
},
]
const CluesScreen = () => {
  const [list, setlist] = useState(listItems[0])
  let params = useParams();
  const [spinner, setspinner] = useState(true)


  const [scannerview, setscannerview] = useState(false)

  const [popup, setpopup] = useState(false)

  const history = useHistory();
  // let params = useParams();

  useEffect(() => {
    if (localStorage.getItem('isFirsttime') === null) {
      loader()
    }
    setspinner(false)
    setlist(listItems[Number(params.id) - 1])
  }, [])

  const updateLSEasUNlocked = (data: string) => {
    console.log("===============")

    var moddata = data.split("/");
    let eventType = moddata[1].substr(0, 4);
    let id = moddata[2]
    console.log(eventType, id)
    if (id === params.id) {

      let a = JSON.parse(String(localStorage.getItem('EventStatusData')))
      let buff: any[] = []
      if (a !== null) {
        a.forEach((ele: any) => {
          if (ele.eventType === eventType && ele.Level === id) {
            console.log(ele)
            if (ele.Status !== "COMPLETED")
              ele.Status = "UNLOCKED"
            buff.push(ele)
            console.log(ele)

          } else {
            buff.push(ele)
          }
        });
      }
      let a1 = JSON.stringify(buff)
      console.log('EventStatusData', a1)
      setTimeout(() => {
        localStorage.setItem('EventStatusData', a1)
        history.replace(data)
      }, 500);
    } else {
      if (id === undefined && eventType=="Comp" && String(params.id) === String(9) ) {
        history.replace(data)
      }
      else {
      console.log(id,"=================================")
      setpopup(true)
      }
    }
  }

  return (
    <div className="back_color_main">
      
      <div>

        <span>
        <img className='bgimgtop' src={backimgtop} />
        </span>
        {spinner ?
          <div style={{
            width: '100vw',
            height: '100vh',
            position: 'absolute',
            top: '0',
            zIndex: 1000,
            backgroundColor: '#00243a',
          }}>
            <Spinner />
          </div> :
          null}

        <HeaderComponent />
        <>
        
          <div className="cluecontainerbefore">
            <div className='PageTitle_OrangeU'>{list.Task_Name}</div>
            <div className='clueimg'>
              <img onClick={() => {
                setscannerview(true)
              }}
                className='clue' src={list.image} />
            </div>

            <div className='clue_logoposition' >
              <img className='clue_logoicon' onClick={() => {
                history.replace('/ClueListScreen/')

              }} src={menu} />
            </div>

            <div className='clueText'>Find this clue and scan the QR CODE!</div>
          </div>

          <div className="bgbottombluebar" />

        </>


      </div>

      {scannerview ?
        <>
          <div className='taskslist_scannerpopup_bg'>
            <div className='taskslist_scannerpopup_container'>

              <div className='QRscannerText'>Scan the QR CODE to unlock the level!</div>

              <div className='taskslist_scannerpopup'>
                <QrReader
                  onScan={(data: any) => {
                    if (data !== null) {
                      console.log(data)
                      let a = data.substring(52, data.length)
                      console.log(a)
                      updateLSEasUNlocked(a)

                    }
                  }}
                  facingMode={"environment"}
                  onError={(data: any) => { console.log(data) }}
                />
                {
                  popup ?
                    <>
                      <div className="popup_bg">
                        <div className="popup_container">
                          <div className="popup">
                            <div className="popup_Text">
                              Oops! Looks like you are at another location, please open the right clue and scan again.
                            </div>
                          </div>
                          <div className='popup_continue_buttonContainer'>
                            <button onClick={() => {
                              history.replace('/ClueListScreen/')
                            }} className="continue_button_Orange">
                              <div className="button_text">Go back</div>
                            </button>
                          </div>
                        </div>
                      </div>
                    </>
                    : null
                }
              </div>
              <div onClick={() => {
                setscannerview(false)
              }
              } className='scanner_close_container'>
                <img className='scanner_close' src={close} />
              </div>
            </div>
          </div>
        </>
        :
        null
      }

    </div>
  )
}

export default CluesScreen
